
          @import "@/assets/css/common.scss";
          @import "@/assets/css/fontface.scss";
        

.modal-background {
  background-color: rgba(black, 0.36);
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;

  display: flex;
  align-items: center;
  justify-content: center;

  .modal-body {
    background-color: white;
    width: 88vw;
    max-height: 72vh;

    transition: .3s;
  }
}

.show-enter-active,
.show-leave-active {
  transition: opacity 0.5s ease;
  .modal-body {
    transition: transform 0.5s ease;
  }
}

.show-enter-from,
.show-leave-to {
  opacity: 0;
  .modal-body {
    transform: translateX(100vw);
  }
}
